.order-reception {
  max-width: 800px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.page-header {
  margin-bottom: 1rem;
}

.page-header h2 {
  color: #0056b3;
  margin-bottom: 0.5rem;
  font-size: 1.8rem;
}

.page-header p {
  color: #6c757d;
  margin: 0;
}

.order-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: #495057;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.input-icon {
  font-size: 1.2rem;
  color: #0056b3;
}

.help-icon-small {
  margin-left: 0.5rem;
  font-size: 0.875rem;
  color: #6366f1;
  cursor: help;
  opacity: 0.7;
  transition: all 0.2s ease;
}

.help-icon-small:hover {
  opacity: 1;
  color: #8b5cf6;
  transform: scale(1.1);
}

.dark .help-icon-small {
  color: #8b5cf6;
}

.dark .help-icon-small:hover {
  color: #a78bfa;
}

.form-group input {
  padding: 0.75rem;
  border: 2px solid #e1e5e9;
  border-radius: 6px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.form-group input:focus {
  outline: none;
  border-color: #0056b3;
  box-shadow: 0 0 0 3px rgba(0, 86, 179, 0.1);
}

.form-group input.error {
  border-color: #dc3545;
}

.form-group input.error:focus {
  box-shadow: 0 0 0 3px rgba(220, 53, 69, 0.1);
}

.error-message {
  color: #dc3545;
  font-size: 0.875rem;
  margin-top: 0.25rem;
}

.form-actions {
  display: flex;
  justify-content: flex-end;
  margin-top: 1rem;
}

.btn {
  padding: 0.75rem 2rem;
  border: none;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 150px;
}

.btn-primary {
  background-color: #0056b3;
  color: white;
}

.btn-primary:hover:not(:disabled) {
  background-color: #004494;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(0, 86, 179, 0.3);
}

.btn-primary:disabled {
  background-color: #6c757d;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

/* AI Fill Section */
.ai-fill-section {
  margin-bottom: 2rem;
}

.ai-fill-toggle {
  width: 100%;
  padding: 1rem 1.5rem;
  background: linear-gradient(135deg, #6366f1 0%, #8b5cf6 100%);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  transition: all 0.3s;
  box-shadow: 0 4px 12px rgba(99, 102, 241, 0.3);
}

.ai-fill-toggle:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(99, 102, 241, 0.4);
}

.ai-fill-panel {
  margin-top: 1rem;
  padding: 1.5rem;
  background: #f8fafc;
  border-radius: 12px;
  border: 2px solid #e2e8f0;
  overflow: hidden;
}

.dark .ai-fill-panel {
  background: #334155;
  border-color: #475569;
}

.ai-fill-panel label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.75rem;
  font-weight: 600;
  color: #1e293b;
}

.dark .ai-fill-panel label {
  color: #e2e8f0;
}

.ai-text-input {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid #e2e8f0;
  border-radius: 8px;
  font-size: 0.95rem;
  font-family: inherit;
  resize: vertical;
  margin-bottom: 1rem;
  transition: border-color 0.2s;
  background: white;
}

.dark .ai-text-input {
  background: #1e293b;
  border-color: #475569;
  color: #e2e8f0;
}

.ai-text-input:focus {
  outline: none;
  border-color: #6366f1;
}

.ai-fill-btn {
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #6366f1 0%, #8b5cf6 100%);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 0.95rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.ai-fill-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.ai-fill-btn:not(:disabled):hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(99, 102, 241, 0.4);
}

@media (max-width: 768px) {
  .form-row {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .form-actions {
    justify-content: center;
  }
  
  .btn {
    width: 100%;
  }
}
